:root {
  --pale-grey: #f2f3fd;
  --dusk: #3c3e58;
  --white: #ffffff;
  --steel: #76778a;
  --bluey-purple: #673dcb;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contact-card {
  margin-left: 18px;
  margin-right: 18px;
  margin-bottom: 16px;
  border-radius: 6px;
  font-family: Lato;
  color: var(--dusk);
  height: 56px;
  box-shadow: 0 3px 11px 0 rgba(129, 129, 129, 0.31),
    0 2px 4px 0 rgba(160, 181, 245, 0.19);
  background-color: var(--white);
}

.contact-card:first-of-type {
  margin-top: 30px;
}

/* Contact Card Content */

.avatar {
  margin-left: 8px;
  margin-top: 8px;
  margin-bottom: 8px;
  width: 48px;
  height: 48px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.avatar img {
  width: 60px;
  height: 60px;
}

.personname {
  margin-top: 11px;
  width: 185px;
  height: 17px;
  font-family: Lato;
  font-size: 13px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--dusk);
}

.description {
  margin-bottom: 4px;
  width: 187px;
  height: 25px;
  font-family: Lato;
  font-size: 9px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--dusk);
}

.person-container {
  margin-left: 9px;
}

.dot {
  height: 12px;
  width: 12px;
  -webkit-align-self: center;
          align-self: center;
  margin-right: 6px;
  border-radius: 6px;
}

.bluey-purple {
  background-color: var(--bluey-purple);
}

.sidebar {
  display: "flex";
  -webkit-flex: 1 1;
          flex: 1 1;
}

.chatwindow {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex: 3 1;
          flex: 3 1;
  background-color: var(--dusk);
}

/* Chat Window */

.inputfield {
  margin-left: 29px;
  margin-right: 29px;
  margin-bottom: 19px;
  height: 48px;
  border-radius: 30px;
  overflow: hidden;
  min-width: 678px;
}

.inputfield input {
  width: 100%;
  height: 100%;
  border-radius: 30px;
  padding-left: 24px;
  padding-right: 24px;
  border: none;
}

.message-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-bottom: 40px;
}

.message-container:nth-of-type(1) {
  height: 87px;
}
.message-container:nth-of-type(2) {
  height: 69px;
  margin-bottom: 21px;
}

/* Message parts */

.avatar-ct {
  width: 48px;
  height: 48px;
  margin-left: 29px;
  margin-right: 29px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.avatar-ct img {
  width: 60px;
  height: 60px;
  overflow: hidden;
}

.message-left,
.message-right {
  padding-top: 10px;
}

.message-left {
  width: 331px;
  height: 87px;
  border-radius: 18px;
  box-shadow: 0 3px 11px 0 rgba(129, 129, 129, 0.31),
    0 2px 4px 0 rgba(160, 181, 245, 0.19);
  background-color: var(--white);
}

.message-right {
  width: 173px;
  height: 48px;
  border-radius: 18px;
  box-shadow: 0 3px 11px 0 rgba(129, 129, 129, 0.31),
    0 2px 4px 0 rgba(160, 181, 245, 0.19);
  background-color: var(--white);
  margin: 16px 18px 15px 18px;
}

.messageText:nth-of-type(1) {
  width: 302px;
  height: 68px;
  font-family: Lato;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--dusk);
  margin: 10px 13px 9px 16px;
}

.messageText:nth-of-type(2) {
  width: 137px;
  height: 17px;
  font-family: Lato;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--dusk);
  margin: 16px 18px 15px 18px;
}

.App {
  background-color: var(--pale-grey);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.fullHeight {
  position: relative;
  height: 100vh;
}

/* Some flexbox styles */
.flex-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.flex-start {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.flex-end {
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

